.tree {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  width: 200px;
  height: 100vh;
  /*overflow-x: hidden;
  overflow-y: auto;*/
  background-color: #21252b;
  font-size: 1.4em;
}

.m-node.placeholder {
  border: 1px dashed #1385e5;
}

.m-node .inner {
  color: #d8e0f0;
  font-size: 12px;
  font-family: Arial, Helvetica, sans-serif;
}

.m-node .node {
  display: inline-block;
  width: 100%;
  padding: 4px 5px;
}

